<template>
  <div id="threeEcharts" :style="{ width: '100%', height: '400px' }" />
</template>

<script>
// import echarts from "echarts";
// require("echarts/theme/macarons"); // echarts theme

export default {
  name: "ThreeChart",
  data() {
    return {
      chart: "linkToHelloWorld",
    };
  },
  mounted() {
    this.initChart();
  },
  beforeDestroy() {},
  methods: {
    // 初始化图表
    initChart() {
      var dom = document.getElementById("threeEcharts");
      var myChart = this.echarts.init(dom);
      //   var app = {}
      //   option = null
      var base = +new Date(1968, 9, 3);
      var oneDay = 24 * 3600 * 1000;
      var date = [];

      var data = [Math.random() * 100];

      for (var i = 1; i < 20000; i++) {
        var now = new Date((base += oneDay));
        date.push(
          [now.getFullYear(), now.getMonth() + 1, now.getDate()].join("/")
        );
        data.push(
          Math.abs(Math.round((Math.random() - 0.5) * 20 + data[i - 1]))
        );
      }

      // 绘制图表
      myChart.setOption({
        tooltip: {
          trigger: "axis",
          position: function(pt) {
            return [pt[0], "10%"];
          },
        },
        title: {
          left: "center",
          top: 15,
          text: "光盘保存日期",
          textStyle: {
            color: "#fff",
            left: "left",
            fontSize: 14,
          },
        },
        xAxis: {
          type: "category",
          boundaryGap: false,
          data: date,
          splitLine: {
            show: false,
          },
          splitArea: {
            show: false,
          },
          axisLine: {
            lineStyle: {
              color: "#fff",
            },
          },
          name: "日期",
          nameTextStyle: {
            color: "#fff",
            fontSize: 12,
            nameGap: 2,
          },
        },
        yAxis: {
          type: "value",
          // boundaryGap: [0, "100%"],
          name: "数量",
          axisLine: {
            lineStyle: {
              color: "#fff",
            },
          },
          splitLine: {
            lineStyle: {
              color: "#595e7f",
            },
          },
        },
        grid: [{ bottom: 90 }, { top: 50 }, { left: 30 }, { right: 30 }],
        dataZoom: [
          {
            type: "inside",
            start: 20,
            end: 80,
          },
          {
            start: 0,
            end: 10,
            handleIcon:
              "M10.7,11.9v-1.3H9.3v1.3c-4.9,0.3-8.8,4.4-8.8,9.4c0,5,3.9,9.1,8.8,9.4v1.3h1.3v-1.3c4.9-0.3,8.8-4.4,8.8-9.4C19.5,16.3,15.6,12.2,10.7,11.9z M13.3,24.4H6.7V23h6.6V24.4z M13.3,19.6H6.7v-1.4h6.6V19.6z",
            handleSize: "80%",
            handleStyle: {
              color: "#fff",
              shadowBlur: 3,
              shadowColor: "rgba(0, 0, 0, 0.6)",
              shadowOffsetX: 2,
              shadowOffsetY: 2,
            },
            textStyle: {
              color: "#fff",
            },
            bottom: 20,
          },
        ],
        series: [
          {
            name: "模拟数据",
            type: "line",
            smooth: true,
            symbol: "none",
            sampling: "average",
            itemStyle: {
              color: "rgba(255, 70, 131,0)",
            },
            areaStyle: {
              color: new this.echarts.graphic.LinearGradient(0, 0, 0, 1, [
                {
                  offset: 0,
                  color: "rgb(48, 210, 118)",
                },
                {
                  offset: 1,
                  color: "rgba(48, 210, 118,0.01)",
                },
              ]),
            },
            data: data,
          },
        ],
      });
    },
  },
};
</script>
